import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../components/helpers/field";
import {
  
  getTglSystem,
} from "../../../components/helpers/function";
import ValidasiTanggalLaporan from "../../../Validasi/ValidasiTanggalLaporan";
import Loading from 'react-fullscreen-loading';

class HeadLihatPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      isLoadingBatal: false,
      FormModal: "",
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }
  componentDidMount() {}

  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-4">
            <label> &nbsp;</label>
            <button
              className="btn btn-primary btn-block"
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Mencari
                </>
              ) : (
                "Cari Penjualan"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadLihatPenjualan = reduxForm({
  form: "HeadLihatPenjualan",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLihatPenjualan);
// export default connect(maptostate, null)(HeadLihatPenjualan);

export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLihatPenjualan);
