import BwipJs from "bwip-js";
import jsPDF from "jspdf";
import "jspdf-autotable";

// const base_url = JSON.parse(localStorage.getItem("base_url"));
import notFound from "../../../../assets/image-not-found.png";
import { getUserdata } from "../../../../components/helpers/function";
const CetakFakturJualBeli = (data = "", kode_baki = "") => {
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || []
  doc.setFontSize(15);
  doc.setFontSize(10);
  doc.setProperties({
    title: "Cetak Faktur Penjualan",
  });
  doc.setFont("tahoma");
  // let notFound = base_url + "image-not-found.png";

  data.notajual.forEach((row, index) => {
    row.Faktur.forEach((list, index) => {
      if(getUserdata().kode_toko==="KNG"){
        doc.setFontSize(6);
      }else{
        doc.setFontSize(10);
      }
      if(list.status===true){
      if (list.jenis === "text") {
        doc.text(
          list.position_x * 0.2645833333,
          (parseFloat(list.position_y) + 15) * 0.2645833333,
          doc.splitTextToSize(list.isi, list.width * 0.2645833333),
          null,
          parseInt(list.rotate * -1)
        );
      } else if (list.jenis === "gambar") {
        if (list.rotate > 0) {
          doc.addImage(
            list.isi,
            "jpg",
            list.position_x * 0.2645833333,
            parseFloat(list.position_y) * 0.2645833333 -
              list.width * 0.2645833333,
            list.width * 0.2645833333,
            list.height * 0.2645833333,
            "",
            "",
            parseInt(list.rotate * -1)
          );
        } else {
          doc.addImage(
            list.isi,
            "jpg",
            list.position_x * 0.2645833333,
            parseFloat(list.position_y) * 0.2645833333,
            list.width * 0.2645833333,
            list.height * 0.2645833333
          );
        }
      } else {
        if (list.rotate > 0) {
          doc.addImage(
            notFound,
            "JPEG",
            list.position_x * 0.2645833333,
            parseFloat(list.position_y) * 0.2645833333 -
              list.width * 0.2645833333 +
              25,
            list.width * 0.2645833333,
            list.height * 0.2645833333,
            "",
            "",
            parseInt(list.rotate * -1)
          );
          // doc.addImage(img.src, 'JPEG', list.position_x * 0.2645833333,
          //   parseFloat(list.position_y) * 0.2645833333,
          //   list.width * 0.2645833333 - 15,
          //   list.height * 0.2645833333,
          //  )
        } else {
          // console.log(list.isi);
          BwipJs.toCanvas("itf", {
            bcid: "code128", // Barcode type
            text: list.isi, // Text to encode
            scale: 3, // 3x scaling factor
            height: 10, // Bar height, in millimeters
            includetext: false, // Show human-readable text
            textxalign: "center", // Always good to set this
          });
          const img = document.getElementById("itf");
          var dataURL = img.toDataURL();
          doc.addImage(
          dataURL,
          "JPEG",
          list.position_x * 0.2645833333,
          parseFloat(list.position_y) * 0.2645833333 -
          list.width * 0.2645833333 + 25,
          list.width * 0.2645833333,
          list.height * 0.2645833333,
          "",
          "",
          );
        }
        }
      }
    });
    if (index < data.notajual.length - 1) {
      doc.addPage();
    }
  });
  // doc.text('NPWP 14.10.431.5-5001.000',8.2020833333,154.78125)
  // doc.addImage(dataURL,"JPEG", 14, 25);
  // doc.autoPrint();
  if (data.notajual.length > 10) {
    doc.save(`Cetak Faktur Penjualan.pdf`);
  } else {
    var string = doc.output("datauristring");
    // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
    var x = window.open();
    x.document.open();
    x.document.write(
      "<html><head><title>Cetak Faktur Penjualan</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
        string +
        "></embed></body></html>"
    );
  }
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default CetakFakturJualBeli;
