import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {
  formatDate,
  localStorageencryp,
} from "../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../components/panel/panel.jsx";
import { postData } from "../../../config/axios.jsx";
// import HeadBatalBeli from "./HeadBatalBeli.jsx";
import HeadLihatPenjualan from "./HeadLihatPenjualan.jsx";
import * as moment from "moment-timezone";
import CetakFakturJualBeli from "../../Kasir/ValidasiJualBeli/pdf/CetakFakturJualBeli.jsx";

// import HeadLihatPenjualan from "./HeadLihatPenjualan.jsx";
class FormLihatPenjualan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingServicesSelesai: false,
      isLoadingBatalBeli: false,
      isLoading: false,
      dataservices: [],
      FormModal: false,
      pembayaran: false,
      datapembelian: [],
      columns: [
        {
          dataField: "no_faktur_jual",
          text: "No FJ",
          footerAttrs: {
            colSpan: "4",
          },
          footer: "Total",
        },
        // {
        //   dataField: "tgl_system",
        //   text: "Tanggal System",

        // },
        {
          dataField: "kode_sales",
          text: "Kode Sales",
        },
        // {
        //   dataField: "kode_member",
        //   text: "Kode Member",
        // },
        // {
        //   dataField: "nama_customer",
        //   text: "Nama Member",
        // },
        // {
        //   dataField: "alamat_customer",
        //   text: "Alamar Customer",
        // },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "harga_jual",
          text: "Harga",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{cell.toFixed(3)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              3
            ),
        },
        {
          dataField: "harga_total",
          text: "Harga Total",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "ambil",
          text: "Reprint",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    data-tooltip="Reprint Penjualan"
                    onClick={() => this.printUlangFaktur(row)}
                    className="btn btn-success mr-3"
                  >
                    <i className="fa fa-file-export"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  printUlangFaktur(row) {
    // console.log(row.no_faktur_beli);
    let data = {
      no_faktur_group: row.no_faktur_group,
    };
    // console.log(data);
    postData("penjualan/get/bynota", data)
      .then((res) => {
        console.log(res.data);
        CetakFakturJualBeli(res.data);
      })
      .catch((err) => {
        ToastNotification("info", "Terjadi Kesalahan Saat Request Data");
      });
  }
  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    localStorageencryp("tgl_laporan", JSON.stringify(data_post));
    this.setState({
      isLoading: true,
    });
    postData("penjualan/get/lihatjual", data_post)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", "Data Penjualan Tidak Ada");
          this.setState({
            isLoading: false,
            datapembelian: [],
          });
        } else {
          ToastNotification("success", "Data Penjualan Tersedia");
          this.setState({
            isLoading: false,
            datapembelian: res.data,
          });
        }
      })
      .catch((err) => {
        //   console.log(err);
        ToastNotification("info", "Data Penjualan Tidak Ada");
        this.setState({
          datapembelian: [],
          isLoading: false,
        });
      });
  }

  render() {
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Kode Member: {row.kode_member} <br />
              Nama Member: {row.nama_customer} <br />
              Nama Atribut : {row.nama_atribut} <br />
              Betat Atribut : {row.berat_atribut.toFixed(2)} <br />
              Harga Atribut : {row.harga_atribut.toLocaleString("kr-KO")} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Penjualan</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Penjualan</li>
        </ol>
        <h1 className="page-header">Lihat Penjualan </h1>
        <Panel>
          <PanelHeader>Lihat Penjualan</PanelHeader>
          <br />
          <PanelBody>
            <HeadLihatPenjualan
              onSubmit={(data) => this.handleSubmit(data)}
              isLoading={this.state.isLoading}
            />
            <Tabel
              keyField="kode_barcode"
              exportCsv={false}
              data={this.state.datapembelian}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Barang Kosong"
              expandRow={expandRow}
            />
          </PanelBody>
          <br />
          {/* End Tambah Lihat Penjualan  */}
        </Panel>
      </div>
    );
  }
}

export default connect(null)(FormLihatPenjualan);
